.projects--heading--dark {
    font-size: 15rem;
    text-align: center;
    border-bottom: 1px solid #fff;
    border-top: 1px solid #fff;
    color: #fff ;
}

.projects--subhead--dark {
    color: #fff;
    margin-top: 50px;
}

.project1--dark {
  margin-top: 50px;
  width: 45%;
}

.project1--dark h1 {
  color: #fff;
  margin-top: 10px;
}

.project1--dark p {
  color: #C0C5D0;
  margin-top: 10px;
}

.project1--dark img {
  width: 100%;
}

.project--tags {
  display: flex;
  gap: 5px;
  margin-top: 15px;
}

.project--tag {
  background-color: #fff;
  font-size: 0.9rem;
  font-weight: 500;
  text-align: center;
  padding: 4px 15px;
  border-radius: 15px;
}

/* Light mode */

.projects--heading--light {
  font-size: 15rem;
  text-align: center;
  border-bottom: 1px solid #1A1A1A;
  border-top: 1px solid #1A1A1A;
  color: #000 ;
}

.projects--subhead--light {
  color: #000;
  margin-top: 50px;
}

.project1--light {
margin-top: 50px;
width: 45%;
}

.project1--light h1 {
color: #000;
margin-top: 10px;
}

.project1--light p {
color: #667085;
margin-top: 10px;
}

.project1--light img {
width: 100%;
}